  .see-pxl {
    margin-right: 10px;
    position: relative;
    cursor: pointer;
  }

  .see-pxl::after {
    content: ' ';
    left: 0;
    position: absolute;
    top: 50%;
    width: 50%;
    height: 2px;
    background-color: white;
    animation-name: strikeIn;
    animation-duration: 0.2s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-fill-mode: both; 
    cursor: pointer;

    
  }

  .pixel-button:hover .see-pxl::after {
    content: ' ';
    position: absolute;
    top: 50%;
    cursor: pointer;
    left: 0;
    width: 50%;
    height: 2px;
    background-color: white;
    animation-name: strike;
    animation-duration: 0.2s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-fill-mode: both; 
  
  }

  .see-pxl-dark::after {
    content: ' ';
    left: 0;
    position: absolute;
    top: 55%;
    width: 50%;
    height: 2px;
    cursor: pointer;
    background-color: black;
    animation-name: strikeIn;
    animation-duration: 0.2s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-fill-mode: both; 
    
  }

  .hire-link:hover .see-pxl-dark::after {
    content: ' ';
    position: absolute;
    top: 55%;
    left: 0;
    width: 50%;
    height: 2px;
    cursor: pointer;
    background-color: black;
    animation-name: strike;
    animation-duration: 0.2s;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    animation-fill-mode: both; 
  
  }

  @keyframes strikeIn{
    0%   { width : 100%; }
    100% { width: 0%; }
  }

